.zad {
    position: relative;
    max-width: 300px;
    width: 100%;
    min-height: 500px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding-top: 15.5vw;
    border: 1px solid white;
  }
  
  .backgroundBlur {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-size: cover;
    background-position: center;
    filter: blur(5px);
    z-index: -1;
  }
  



.songname {
    font-family: 'Protest Revolution', sans-serif;
    font-size: 27px;
}

.author {
    font-family: 'Bebas Neue', sans-serif;
    font-size: 22px;
    opacity: 0.7;
}







.cont {
    display: flex;
    gap: 40px;
    margin: 0 auto;
}

.cont button {
    border-radius: 20px;
    width: 50px;
    height: 50px;
    background-color: black;
    color: white;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: 0.7s;
}

.cont button:hover {
    transform: scale(1.1);
}

.cont .pause {
    width: 70px;
    background-color: white;
    color: black;
    font-size: 30px;
}


.like, .prev {
    width: 40px;
    height: 40px;
    font-size: 40px;
    background: none;
    border: none;
    position: absolute;
    top: 0;
    right: 5%;
    cursor: pointer;
    transition: 0.7s;
}

.like:hover, .prev:hover {
    transform: scale(1.1);
}

.prev {
    left: 0;
}

.foot {
    display: flex;
    gap: 10vw;
    align-items: center;
    justify-content: center;
}

.foot button {
    width: 30px;
    height: 30px;
    font-size: 25px;
    background: none;
    color: white;
    border: none;
    cursor: pointer;
    transition: 0.7s;
}

.foot button:hover {
    color: lightgray;
}