.zad {
    position: relative;
    max-width: 600px;
    width: 100%;
    min-height: 500px;
    display: flex;
    gap: 50px;
    background-color: white;
    border: 1px solid black;
    align-items: flex-start;
    justify-content: center;
    font-family: Arial, Helvetica, sans-serif;
    padding-top: 2vw;
  }
  
  .zad.dark {
    background-color: black;
    color: white;
    border: 1px solid white;
  }

  .zad.dark .btn {
    color: white;
  }


  .zad.dark .right ul li {
    border-bottom: 1px solid white;
  }

  .zad.dark .photo {
    border: 3px solid white;
  }
  
  .zad.light {
    background-color: white;
    color: black;
  }
  



  .left {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  
  .photo {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
    border: 3px solid black;
  }

  .right {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  .right ul {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    gap: 5px;
  }

  .right ul li {
    border-bottom: 1px solid black;
  }

  .right h1 {
    font-size: 24px;
  }
  
  .right p {
    margin: 5px 0;
    font-size: 16px;
  }

  .btn {
    background: none;
    border: none;
    width: 40px;
    font-size: 30px;
    height: 40px;
    position: absolute;
    bottom: 2%;
    left: 2%;
    transition: 0.7s;
    cursor: pointer;
  }

  .btn:hover {
    transform: scale(1.2);
  }








  @keyframes scaleInOut {
    0% {
      transform: scale(0.95);
      opacity: 0;
    }
    50% {
      transform: scale(1.05);
      opacity: 0.5;
    }
    100% {
      transform: scale(1);
      opacity: 1;
    }
  }
  
  .zad.animate {
    animation: scaleInOut 0.6s ease-in-out;
  }