.title {
    font-size: 25px;
    font-style: italic;
    color: darkred;
}

.zad {
    max-width: 700px;
    width: 100%;
    min-height: 300px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    background-color: #333;
    padding: 20px 0px;
    border: 1px solid white;
}

.zad p {
    font-size: 15px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
}

.spisok {
    display: flex;
    flex-direction: column;
    gap: 1.5vw;
}

.flag {
    max-width: 80px;
    vertical-align: middle;
}

.line {
    background-color: black;
    height: 3px;
    min-width: 80%;
    margin: 0 auto;
}